<template>
  <div>
    <el-dialog
      @opened="openDialog"
      :width="dialogWidth"
      :title="title"
      :visible.sync="showEditDialog"
      :show-close="false"
      :close-on-click-modal="false"
    >
      <el-form
        :model="forms"
        :rules="rules"
        ref="editForms"
        class="edit-forms"
        label-position="left"
        :label-width="labelWidth"
      >
        <el-form-item label="标题" prop="noticeTitle">
          <el-input v-model="forms.noticeTitle" placeholder="标题"></el-input>
        </el-form-item>
        <el-form-item label="分类" prop="noticeType">
          <el-select
            v-model="forms.noticeType"
            clearable
            style="width: 140px"
            placeholder="类型"
            size="small"
          >
            <el-option v-for="(k,v,index) in noticeTypeEnum" :key="index" :label="k" :value="v*1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="图片" prop="noticePic">
          <el-upload
            class="avatar-uploader"
            action
            :show-file-list="false"
            :http-request="uploadImage"
          >
            <img v-if="forms.noticePic" :src="forms.noticePic" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="简介">
          <el-input type="textarea" :rows="2" placeholder="请输入内容" v-model="forms.noticeBrief"></el-input>
        </el-form-item>
        <el-form-item label="内容" style="height:250px">
          <quill-editor
            v-model="forms.noticeContent"
            ref="myQuillEditor"
            style="height: 200px;"
            :options="editorOption"
          ></quill-editor>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="$emit('close')">取 消</el-button>
        <el-button type="primary" @click="submit">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Required } from "@/utils/validateRules";
import { fillerLeft, resetObject } from "@/utils/common";
import { getDateStr } from "@/utils/date";
import { editNotice } from "@/api/sys/sysManage";
import { uploadImage } from "@/api/goods/goodsManage";
import { NoticeTypeEnum, NoticeStatusEnum } from "@/utils/Immutable";
import { quillEditor } from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
export default {
  components: {
    quillEditor
  },
  props: {
    title: {
      type: String,
      default: "编辑"
    },
    dialogWidth: {
      type: String,
      default: "800px"
    },
    labelWidth: {
      type: String,
      default: "60px"
    },
    showEditDialog: Boolean,
    editorOption: {}
  },
  data() {
    return {
      forms: {
        noticeType: 1,
        noticeTitle: "",
        noticePic: "",
        noticeBrief: "",
        noticeContent: ""
      },
      noticeTypeEnum: NoticeTypeEnum,
      noticeStatusEnum: NoticeStatusEnum,
      noticeTypes: [],
      rules: {
        noticePic: Required,
        noticeTitle: Required
      }
    };
  },
  methods: {
    submit() {
      this.$refs.editForms.validate(valid => {
        if (valid) {
          editNotice(this.forms).then(data => {
            if (data.code == 0) {
              this.$message.success("更新成功！");
              this.$emit("success");
              return;
            }
            this.$message.error(data.msg);
          });
          return;
        }
        this.$message.error("请按照提示正确填写内容！");
        return false;
      });
    },
    //任务图标文件上传成功后
    async uploadImage(fileObj) {
      this.forms.noticePic = await this.upload(fileObj);
    },
    async upload(fileObj) {
      if (!this.beforeAvatarUpload(fileObj.file)) return;
      let formData = new FormData();
      formData.set("file", fileObj.file);
      const data = await uploadImage(formData);
      if (data.code != 0) {
        this.$message.error(data.msg);
        return "";
      }
      console.log(data.data);
      return data.data;
    },
    //文件上传前
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg" || file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    openDialog() {},
    add() {
      this.forms = {
        noticeType: 1,
        noticeTitle: "",
        noticePic: "",
        noticeBrief: "",
        noticeContent: ""
      };
    },
    update(id, data) {
      //可以外部填充回写做编辑用，也可以请求详情接口填充表单
      this.$set(this.forms, "id", id);
      fillerLeft(this.forms, data);
    }
  }
};
</script>
<style lang="scss">
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}
.my-icon {
  font-size: 20px;
  position: absolute;
  left: 60px;
  top: 0px;
  color: red;
}
</style>