<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="标题"
          size="small"
          style="width: 140px"
          v-model="searchParams.noticeTitle"
          clearable
        ></el-input>
        <el-select
          v-model="searchParams.noticeType"
          clearable
          style="width: 140px"
          placeholder="类型"
          size="small"
        >
          <el-option v-for="(k,v,index) in noticeTypeEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-date-picker
          v-model="searchParams.dateRange"
          size="small"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
        ></el-date-picker>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
      <div>
        <el-button type="primary" size="small" @click="add()">添加</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left">
            <el-form-item label="简介">
              <span>{{ props.row.noticeBrief }}</span>
            </el-form-item>
            <el-form-item label="内容">
              <span v-html=" props.row.noticeContent"></span>
            </el-form-item>
            <el-form-item label="备注">
              <span>{{ props.row.remark }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="noticeTitle" label="标题" :formatter="contentFormatter"></el-table-column>
      <el-table-column align="center" prop="noticeType" label="分类" :formatter="formmaterNotice"></el-table-column>
      <el-table-column align="center" label="图片">
        <div slot-scope="s">
          <el-image style="width: 80px; height: 80px" :src="s.row.noticePic" fit="fill"></el-image>
        </div>
      </el-table-column>
      <el-table-column
        align="center"
        prop="noticeStatus"
        label="状态"
        :formatter="formmaterNotice"
        width="100"
      >
        <div slot-scope="s">
          <el-select
            v-model="s.row.noticeStatus"
            placeholder="请选择"
            size="mini"
            @change="changeStatus(s.row)"
          >
            <el-option v-for="(k,v,index) in noticeStatusEnum" :key="index" :label="k" :value="v"></el-option>
          </el-select>
        </div>
      </el-table-column>
      <el-table-column align="center" prop="createBy" label="创建人"></el-table-column>
      <el-table-column align="center" prop="createTime" label="创建时间"></el-table-column>
      <el-table-column align="center" prop="updateBy" label="修改人"></el-table-column>
      <el-table-column align="center" prop="updateTime" label="修改时间"></el-table-column>

      <el-table-column align="center" label="操作">
        <div slot-scope="s">
          <el-button type="primary" size="small" @click="edit(s.row)">编辑</el-button>
          <el-button type="danger" size="small" @click="removeItem(s.row)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getNoticeList"
      ref="pagination"
      @returnData="returnData"
    />
    <NoticeEdit
      :title="titie"
      ref="edit"
      :showEditDialog="showEditDialog"
      @close="showEditDialog = false"
      @success="refresh"
    />
  </div>
</template>

<script>
import { getNoticeList, deleteNotice, editNotice } from "@/api/sys/sysManage";
import { getDateList } from "@/utils/date";
import { resetObject } from "@/utils/common";
import { NoticeTypeEnum, NoticeStatusEnum } from "@/utils/Immutable";
import NoticeEdit from "./NoticeEdit";
export default {
  components: { NoticeEdit },
  data() {
    return {
      searchParams: {
        noticeTitle: "",
        noticeType: "",
        dateRange: []
      },
      noticeTypeEnum: NoticeTypeEnum,
      noticeStatusEnum: NoticeStatusEnum,
      pickerOptions: getDateList(),
      getNoticeList: getNoticeList,
      titie: "添加",
      showEditDialog: false,
      tableData: []
    };
  },

  methods: {
    contentFormatter(value, index, cell) {
      return cell.slice(0, 20);
    },
    formmaterNotice(a, b, v) {
      return this.noticeTypeEnum[v];
    },
    formmaterNoticeStatus(a, b, v) {
      return this.noticeTypeEnum[v];
    },
    async changeStatus(v) {
      let data = await editNotice(v);
      if (data.code != 0) {
        this.$message.error(data.msg);
        return;
      }
    },
    edit(row) {
      this.titie = "修改";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, row);
    },
    add() {
      this.titie = "添加";
      this.showEditDialog = true;
      this.$refs.edit.add();
    },
    removeItem(item) {
      this.$confirm("此操作将永久删除该配置吗, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteNotice(item.id).then(res => {
            if (res.code == 0) {
              this.$message.success("删除成功!");
              this.refresh();
              return;
            }
            this.$message.error(res.msg);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    returnData(data) {
      this.tableData = data;
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    }
  }
};
</script>
