import request from "@/utils/request.js";

/**
 * 配置管理
 */

export function getConfigList(params) {
    return request({
        url: "/apiadmin/config/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function editConfig(params) {
    return request({
        url: "/apiadmin/config/edit",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}
export function deleteConfig(id) {
    return request({
        url: "/apiadmin/config/delete/" + id,
        method: "get",
    });

}
/**
 * 公告管理
 */

export function getNoticeList(params) {
    return request({
        url: "/apiadmin/notice/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function editNotice(params) {
    return request({
        url: "/apiadmin/notice/edit",
        method: "post",
        data: params //注意：如果是get请求请使用 params: params
    });
}
export function deleteNotice(id) {
    return request({
        url: "/apiadmin/notice/delete",
        method: "get",
        params: { id: id }
    });

}

